@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
.square {
  border-color: #444;
  border-style: solid;
  border-width: 0 3px 3px 0;
  font-size: 35px;
  line-height: 70px;
  height: 70px;
  padding: 0;
  text-align: center;
  width: 70px;
  display: block;
}

.square:focus {
  outline: none;
}

.board {
  display: flex;
  align-content: center;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
}

.board-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.board-row .square:last-child {
  border-right: 0px;
}

.board-row:last-child .square {
  border-bottom: 0px;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-board,
.game-info {
  display: flex;
}

.game-board {
  flex-grow: 1;
  background: #fdfdfd;
}

.game-info {
  width: 25%;
  max-width: 180px;
  background: #f0f0f0;
  flex-direction: column;
}

.game-status {
  display: flex;
  font-size: 18px;
  line-height: 2.5em;
  margin-bottom: 20px;
}

.game-status .phrase {
  flex-grow: 1;
  padding: 0 10px;
  background: #3b3b3b;
  color: #fdfdfd;
}

.game-status .player {
  flex-grow: 0;
  width: 2.5em;
  text-align: center;
  background: #d0d0d0;
}

.history {
  display: flex;
  flex-direction: column-reverse;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.history li {
  margin-bottom: 15px;
}

.history li .square {
  border-width: 0 2px 2px 0;
  font-size: 18px;
  line-height: 40px;
  height: 40px;
  padding: 0;
  text-align: center;
  width: 40px;
  display: block;
}

@media all and (max-width: 765px) {
  .game {
    flex-direction: column;
  }

  .game-info {
    max-width: 100%;
    width: 100%;
    height: 200px;
  }

  .history {
    flex-direction: row;
    justify-content: space-around;
    max-height: 139px;
    overflow: scroll;
  }

  .history li {
    flex: 0 0 auto;
    width: 25%;
  }
}

@media all and (max-width: 540px) {
  .history li {
    flex: 0 0 auto;
    width: 33%;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body,
html,
#root,
.game {
  width: 100%;
  height: 100%;
}

body {
  font: 14px "Lato", Futura, sans-serif;
}

ol,
ul {
  padding-left: 30px;
}

.status {
  margin-bottom: 10px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

